        color="green"
      >
        <v-icon>edit</v-icon>
      </v-btn> -->
            <v-card :color="list.color" hover  v-for="card in list.cards" :key="card.id" class="ma-2" :id="card.id" @mousedown="editcard(card);selectedlist.name = list.name;selectedlist.id = list.id;selectedlist.color = list.color">
              <v-tooltip right>
              <v-card-title primary-title slot="activator">
                <div class="body-2">{{card.name}}</div>
              </v-card-title>
              <div v-for="desc in card.desc">
                <div v-if=" typeof(desc) != 'object' && desc != ''">
                  <span class="body-2">{{desc}}</span>
                </div>
              </div>
              <!-- <div>
                <v-chip small color="pink lighten-1" text-color="white" v-for="person in card.desc.people" :key="person.id" >
                  {{person.name}}
                </v-chip>
              </div>
              <div>
                <v-chip small color="pink lighten-1" text-color="white" v-for="data in card.desc.data" :key="data.id" >
                  {{data.name}}
                </v-chip>
              </div> -->
              <!-- <v-card>
        <v-list>
          <v-list-tile v-for="desc in card.desc">
            <v-list-tile-content v-if=" typeof(desc) != 'object' && desc != ''">
               <v-list-tile-title>
                 {{desc}}
               </v-list-tile-title>
                 <v-chip small color="pink lighten-1" text-color="white" v-for="person in card.desc.people" :key="person.id" >
      {{person.name}}
    </v-chip>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
              </v-card> -->
              <!-- <v-card-actions style=" background-color:white">  
                <v-btn icon flat color="grey" :to="{name:'editboard',params:{id:board.id}}"><v-icon>edit</v-icon></v-btn>
                <v-btn icon flat color="grey" :to="{name:'index'}" active-class @click.native.stop="dialog=true;selectedid=board.id"><v-icon>delete</v-icon></v-btn>
              </v-card-actions> -->
     <!--          <v-speed-dial
      v-model="fab"
      :open-on-hover="true"
      :direction="'right'"
    >
               <v-btn
              color="dark"
              dark
              small
              absolute
              bottom
              right
              fab
              slot="activator"
              v-model="fab"
            >
              <v-icon>add</v-icon>
            </v-btn>
              </v-speed-dial> -->
              </v-tooltip>
            </v-card>
    <!-- </v-speed-dial> -->
                    </draggable>
          </v-container>
    
      <v-footer><v-btn color="black" dark style="margin:0;width:100%" class="subheading" @click.native.stop="dialog = true; selectedlist.name = list.name;selectedlist.id = list.id;selectedlist.color = list.color">新增卡片
              <v-icon dark right>add</v-icon>
            </v-btn></v-footer>


      </v-card>
    </v-flex>
  </v-layout>
  <v-dialog v-model="dialog" persistent max-width="50em">
        <v-card>
          <!-- <v-snackbar v-model="snackbar" absolute top right color="success">
            <span>新增成功!</span>
          <v-icon dark>check_circle</v-icon>
          </v-snackbar> -->
          <v-form ref="form" v-model="valid" lazy-validation @submit.prevent="submit">
            <v-container>
              <v-layout row wrap>
                <v-flex d-flex md6>
                  <v-card :color='selectedlist.color'>
                   <v-card-title primary-title>
                {{card.title}}
              </v-card-title>
            <!--   <v-card-text>
                 <v-chip small color="pink lighten-1" text-color="white" v-for="person in card.desc.people" :key="person.id" >
                  {{person.name}}
                </v-chip>
              </v-card-text> -->
                  </v-card>
                </v-flex>
                <v-flex d-flex md6>
                  <v-layout row wrap>
                    <v-flex d-flex xs12>
   <div>
                   
       <v-chip small color="pink lighten-1" text-color="white" v-for="person in card.desc.people" :key="person.id" >
                  {{person.name}}
                </v-chip>
   </div>
                    </v-flex>
                     <v-flex d-flex xs12>
           <div>
       <v-chip small color="pink lighten-1" text-color="white" v-for="data in card.desc.data" :key="data.id" >
                  {{data.name}}
                </v-chip>
           </div>
                    </v-flex>
                  </v-layout>
                </v-flex>
                <v-flex d-flex md12 class="mt-3">
                  <v-layout row wrap v-if="selectedlist.name =='問題面向'">
                    <v-flex d-flex xs12>
                   <v-text-field label="問題面向" prepend-icon="announcement" v-model="card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row wrap v-if="selectedlist.name == '問題細節'">
                         <v-flex d-flex xs12>
                   <v-text-field label="問題細節" prepend-icon="announcement" v-model="card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                     <v-flex d-flex xs12>
                   <v-text-field label="補充說明" prepend-icon="people" v-model="card.desc.explain" ></v-text-field>
                    </v-flex>
                  </v-layout>
                      <v-layout row wrap  v-if="selectedlist.name == '解法'">
                         <v-flex d-flex xs12>
                   <v-text-field label="解法" prepend-icon="announcement" v-model="card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                      </v-layout>
                      <v-layout row wrap  v-if="selectedlist.name == '回應'">
                        <v-flex d-flex xs12>
                   <v-text-field label="回應" prepend-icon="announcement" v-model="card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                      <v-flex d-flex xs12>
                   <v-text-field label="補充說明" prepend-icon="people" v-model="card.desc.explain"  ></v-text-field>
                    </v-flex>
                        </v-layout>
                      <v-layout row wrap v-if="selectedlist.name == '困難'">
                     <v-flex d-flex xs12>
                   <v-text-field label="困難" prepend-icon="announcement" v-model="card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                      <v-flex d-flex xs12>
                   <v-text-field label="補充說明" prepend-icon="people" v-model="card.desc.explain"  ></v-text-field>
                    </v-flex>
                        </v-layout>
                      <v-layout row wrap v-if="selectedlist.name == '利害關係人'">
                      <v-flex d-flex xs12>
                   <v-text-field label="利害關係人" prepend-icon="announcement" v-model="card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                     <v-flex d-flex xs12>
                   <v-text-field label="單位" prepend-icon="announcement" v-model="card.desc.department" ></v-text-field>
                    </v-flex>
                     <v-flex d-flex xs12>
                   <v-text-field label="背景" prepend-icon="announcement" v-model="card.desc.background"  ></v-text-field>
                    </v-flex>
                        </v-layout>
                      <v-layout row wrap v-if="selectedlist.name == '資料/文件/連結'">
                      <v-flex d-flex xs12>
                   <v-text-field label="資料/文件/連結" prepend-icon="announcement" v-model="card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                      <v-flex d-flex xs12>
                   <v-text-field label="摘要" prepend-icon="people" v-model="card.desc.summary"></v-text-field>
                    </v-flex>
                    <v-flex d-flex xs12>
                   <v-text-field label="歸納" prepend-icon="people" v-model="card.desc.induction" ></v-text-field>
                    </v-flex>
                        </v-layout>
                
                </v-flex>

                <v-flex d-flex md12>
                  <v-layout row wrap v-if="selectedlist.name != '資料/文件/連結' && selectedlist.name != '利害關係人'">
                    <v-flex d-flex xs12 >
                        <v-select
                          v-model="card.desc.people"
                          :items="peoplelist"
                          item-text="name"
                          label="關聯利害關係人"
                          prepend-icon="people"
                          chips
                          tags
                        ></v-select>
                    </v-flex>
                  </v-layout>
                </v-flex>
                  <v-flex d-flex md12>
                  <v-layout row wrap v-if="selectedlist.name != '資料/文件/連結' && selectedlist.name != '利害關係人'">
                    <v-flex d-flex xs12 >
                        <v-select
                          v-model="card.desc.data"
                          :items="datalist"
                          item-text="name"
                          label="佐證文件"
                          prepend-icon="picture_as_pdf"
                          chips
                          tags
                        ></v-select>
                    </v-flex>
                  </v-layout>
                </v-flex>
              </v-layout>
            </v-container>
               <v-card-actions>
               <!--  <v-btn :disabled="!formIsValid" flat color="primary" type="submit" class="subheading">確認</v-btn> -->
               <v-btn flat color="primary" type="submit" class="subheading">確認</v-btn>
                <v-spacer></v-spacer>
        <v-btn flat class="subheading" @click="resetForm">清除</v-btn>
        <v-spacer></v-spacer>
         <v-btn flat @click.native="closeDialog" class="subheading">關閉</v-btn>
          </v-card-actions>
          </v-form>
        </v-card>
      </v-dialog>
  </v-container>
</template>

<script>
import draggable from 'vuedraggable'
import newcard from './newcard'
export default {
  components: {
    draggable,
    newcard
  },
  data () {
    /* const defaultForm = Object.freeze({
      card: {
        title: '',
        desc: '',
        department: '',
        background: '',
        summary: '',
        induction: ''
      }
    }) */
    return {
      board: {},
      lists: [],
      cards: [],
      dialog: false,
      selectedlist: {},
      snackbar: false,
      valid:false,
      titleRules: [
        v => !!v || '此欄位為必填!',
        v => v.length <= 20 || '此欄位不可超過20個字!'
      ],
      /* items: [
        'Programming',
        'Design',
        'Vue',
        'Vuetify',
      ], */
      card: {
        id: '',
        title: '',
        desc:{
          explain:'',
          department: '',
          background: '',
          summary: '',
          induction: '',
          people: [],
          data: []
        }
      },
      peoplelist: [],
      datalist: [],
      fab: false,
      editable: false
      /* form: Object.assign({}, defaultForm), */
    }
  },
  methods: {
    getcards: function(id) {
      let newcards = []
      for (let c of this.cards) {
        if (c.idList == id) {
          newcards.push(c)
        }
      }
      return newcards
    },
    movecard: function(event) {
      let that = this
      Trello.put('cards/' + event.item.id,{'idList':event.to.id},function(res) {
      })
    },
    closeDialog: function() {
      this.dialog = false
    },
    resetForm: function() {
      /* this.form = Object.assign({}, this.defaultForm) */
      this.$refs.form.reset()
    },
    submit: function() {
      let that = this
      if (this.editable = false) 
      {
        Trello.post('cards', {'name': this.card.title, 'idList': this.selectedlist.id,'desc': JSON.stringify(this.card.desc) } , function() {
          window.location.reload(true);
        })
      }
      else {
        Trello.put('cards/' + this.card.id, {'name': this.card.title, 'idList': this.selectedlist.id,'desc': JSON.stringify(this.card.desc) } , function() {
          window.location.reload(true);
        })
      }
    },
    getpeople: function() {
      this.lists.map(list => {
        if (list.name == '利害關係人') {
          list.cards.map( card => {
            this.peoplelist.push(card)
          })
        }
      })
    },
    getdata: function() {
      this.lists.map(list => {
        if (list.name == '資料/文件/連結') {
           list.cards.map( data => {
            this.datalist.push(data)
          })
        }
      })
    },
    editcard: function(card) {
      this.dialog = true
      this.card.id = card.id
      this.card.title = card.name
      this.card.desc = card.desc
      this.editable = true
    }
  },
  created: function() {
    let that = this;
    this.board.id = this.$route.params.id
    Trello.boards.get(this.board.id, function(res) {
      that.board.name = res.name
      if (res.desc != '') {
        that.board.desc = JSON.parse(res.desc)
      }
    })
    Trello.boards.get(this.board.id + '/lists',{cards: 'open'}, function(res) {
      /* for (let i in res) {
        let list = {}
        list.id = res[i].id
        list.name = res[i].name
        list.cards = res[i].cards
        switch (list.name)
        {
          case '資料/文件/連結':
          list.color = 'blue accent-1'
          break
          case '問題面向':
          list.color = 'yellow lighten-1'
          break
          case '問題細節':
          list.color = 'yellow lighten-3'
          break
          case '解法':
          list.color = 'light green accent-3'
          break
          case '回應':
          list.color = 'orange'
          break
          case '困難':
          list.color = 'pink'
          break
          case '利害關係人':
          list.color = 'indigo'
          break
          default:
          list.color = 'teal'
          break
        }
        that.lists.push(list)
      } */
      res.map( l => {
        let list = {}
        list.id = l.id
        list.name = l.name
        list.cards = l.cards
        switch (list.name)
        {
          case '資料/文件/連結':
          list.color = 'blue accent-1'
          break
          case '問題面向':
          list.color = 'yellow lighten-1'
          break
          case '問題細節':
          list.color = 'yellow lighten-3'
          break
          case '解法':
          list.color = 'light green accent-3'
          break
          case '回應':
          list.color = 'orange'
          break
          case '困難':
          list.color = 'pink'
          break
          case '利害關係人':
          list.color = 'indigo'
          break
          default:
          list.color = 'teal'
          break
        }
        /* console.log(list) */
        list.cards.map(card => {
          if (card.desc != '') {
            let desc = JSON.parse(card.desc)
            card.desc = desc
            console.log(desc)
          }
        })
        that.lists.push(list)
      })
      that.getpeople()
      that.getdata()
    })
    /* Trello.cards.get("Uta5z7Fr/attachments", function(res) {
      console.log(res)
    }) */
  },
}
</script>

